import React from "react";
import scss from "./StudentsClass.module.scss";

const StudentsClass = () => {
    return (
        <section className={scss.StudentsClass}>
            <div className="container">
                <div className={scss.content}>
                    <h2 className={scss.title}>4 Класс</h2>
                    <div className={scss.table}>
                        <div className={scss.tableTitle}>
                            <h1 className={scss.number}>No.</h1>
                            <h1 className={scss.name}>Имя ученика</h1>
                            <h1 className={scss.class}>Класс</h1>
                            <h1 className={scss.teacher}>Кл. руководитель</h1>
                        </div>
                        <div className={scss.tableContent}></div>
                    </div>
                </div>
            </div>
        </section>
    );
};

export default StudentsClass;
